import React from 'react';
import Colcade from 'colcade';
export default ({photos})=>{
    let imgMapped = () => photos.map((each,idx) => {
        return <div key={each.src + idx} className="grid-item"><img src={each.src} alt="First slide" /></div>
    });

    return (<div className="grid d-flex justify-content-center pt-5" data-colcade="columns: .grid-col, items: .grid-item">
    <div className="grid-col grid-col--1"></div>
    <div className="grid-col grid-col--2"></div>
    <div className="grid-col grid-col--3"></div>
    <div className="grid-col grid-col--4"></div>
  
    {imgMapped()}
  </div>);
}